<template>
	<div class="headers">
		<div id="xgmm">
			<div class="tips">
				溫馨提示：根據內容提示，您可以對您的各項密碼進行重置。請妥善保管好您已更新的密碼！
			</div>
			<form action="/index.php/member/new/account/userpwd" method="post" name="add_form" onsubmit="return check_submit1()" id="add_form2">
				<p><label>請選擇要修改的密碼：</label>
					<select onchange="selecttype.submit();" name="pwdtype" id="myType">
						<option value="userpwd">登錄密碼</option>
						<option value="moneypwd">取款密碼</option>
					</select>
				</p>
				<div style="display: none;" id="mpb">
					<p><label id="mpbname"></label>
					</p>
				</div>
				<div style="display: bloack;" id="mpb_a">
					<p><label id="ypwd">原密碼：</label><input id="oldpass" class="input_150" maxlength="12" type="password" name="oldpass" value="" onkeyup="value=value.replace(/[^0-9a-zA-Z]/g,'')"></p>
					<p><label id="xpwd">新密碼：</label><input id="newpass" class="input_150" maxlength="12" type="password" name="newpass" value="" onkeyup="value=value.replace(/[^0-9a-zA-Z]/g,'')"></p>
					<p><label>確認新密碼：</label><input id="newpass2" class="input_150" maxlength="12" type="password" name="newpass2" value="" onkeyup="value=value.replace(/[^0-9a-zA-Z]/g,'')"></p>
				</div>
				<p><label style="float: left;"></label><input type="submit" value="確認修改" class="Confirm"><input type="reset" value="重置" class="Confirm"></p>
			</form>
			<div class="txt">
				<p id="userpwd">●您的新密碼長度需介於6-12個字符之間。<br> ●您的取款新密碼衹需4個字符。
					<br> ●您的密碼只能包含英文字母（A-Z），數字（0-9）以及（.）符號。
					<br> ●您的密碼至少包含一個英文字母以及一個數字。
					<br> ●您的密碼不能與您的賬號名過於相似。
					<br> ●新密碼與再次輸入新密碼必須相同。
				</p>
				<p id="moneypwd" style="display: none">● 您的新密码只需4个字符。<br> ● 您的新密码只能包含英文字母(A-Z，区分大小写)、数字（0-9）。<br> ● 新密码与确认新密码必须相同。
				</p>

			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {

			}
		},
		methods: {

		},
		mounted: function() {
			let off = true;
			$('.tzzxSport').click(function() {
				if(off) {
					$(this).find('tbody tr').css('display', 'table-row');
					off = false;
				} else {
					$(this).find('tbody tr').css('display', 'none');
					off = true
				}
			})
		}
	}
</script>
<style rel="stylesheet" type="text/css" src="../../style/grzx.css" scoped="scoped" /></style>


